import React from 'react';

import PropTypes from 'prop-types';

import AppMenu from '../components/app-menu';
import Image from '../img/paper_bg.png';

import Drawer from '@material-ui/core/Drawer';
import Hidden from '@material-ui/core/Hidden';
import IconButton from '@material-ui/core/IconButton';

import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import Paper from '@material-ui/core/Paper';
import { makeStyles, useTheme } from '@material-ui/core/styles';

import MenuIcon from '@material-ui/icons/Menu';

if (typeof window !== 'undefined'){
  require('smooth-scroll')('a[href*="#"]')
}


const drawerWidthMd = 384;
const drawerWidthSm = 316;

const useStyles = makeStyles((theme) => ({
  root: { 
    [theme.breakpoints.up('md')]: {
      display: 'flex',
    }
  },
  drawer: {
    [theme.breakpoints.up('md')]: {
      width: drawerWidthMd,
      flexShrink: 0,
    },
    [theme.breakpoints.up('sm')]: {
      width: drawerWidthSm,
    },
  },
  appBar: {
    position: 'fixed',
    margin: 0,
    [theme.breakpoints.up('md')]: {
      display: 'none',
    },
  },
  menuButton: {
    marginRight: theme.spacing(2),
    [theme.breakpoints.up('md')]: {
      display: 'none',
    },
  },
  toolbarTypography:{
    [theme.breakpoints.up('md')]: {
      display: 'none',
    },
  },
  // necessary for content to be below app bar
  toolbar: theme.mixins.toolbar,
  drawerPaper: {
    width: drawerWidthSm,
    whiteSpace: 'nowrap',
    paddingTop: theme.spacing(1),
    paddingBottom: theme.spacing(1),
    color: 'rgba(255,255,255,1.0)',
    backgroundColor: 'rgb(66,66,66)',
  },
  content: {
    flexGrow: 1,
    position: 'relative',
    backgroundImage: `url(${Image})`,
  },
  appMenu: {
    height: 'auto'
  }
}));

function ResponsiveNavBar(props) {
  console.log('Props', props)
  const { window } = props;
  const classes = useStyles();
  const theme = useTheme();
  const [mobileOpen, setMobileOpen] = React.useState(false);

  const handleDrawerToggle = () => {
    setMobileOpen(!mobileOpen);
  };

  const container = window !== undefined ? () => window().document.body : undefined;

  return (
    <div className={classes.root} >
      <AppBar className={classes.appBar}>
        <Toolbar className={classes.toolbar}>
          <IconButton
            color="inherit"
            aria-label="open drawer"
            edge="start"
            onClick={handleDrawerToggle}
            className={classes.menuButton}
          >
            <MenuIcon />
          </IconButton>
          <Typography variant="h6" noWrap className={classes.toolbarTypography}>
            Open Legend
          </Typography>
        </Toolbar> 
      </AppBar>
      <nav className={classes.drawer} aria-label='links'>
        <Hidden mdUp implementation="css">
          <Drawer
            container={container}
            variant="temporary"
            anchor={theme.direction === 'rtl' ? 'right' : 'left'}
            open={mobileOpen}
            onClose={handleDrawerToggle}
            classes={{
              paper: classes.drawerPaper,
            }}
            ModalProps={{
              keepMounted: true, // Better open performance on mobile.
            }}
          >
            <AppMenu className={classes.appMenu} /> 
          </Drawer>
        </Hidden>
        <Hidden smDown implementation="css">
          <Drawer
            classes={{
              paper: classes.drawerPaper,
            }}
            variant="permanent"
            open
          >
            <AppMenu className={classes.appMenu}  />  
          </Drawer>
        </Hidden>
      </nav>
      <main className={classes.content}>
        {props.children}
      </main>
    </div>
  );
}

ResponsiveNavBar.propTypes = {
  /**
   * Injected by the documentation to work in an iframe.
   * You won't need it on your project.
   */
  window: PropTypes.func,
};

export default ResponsiveNavBar;