const useStyles = makeStyles((theme) => {
  createStyles({
    container: {
      alignItems: 'space-between'
    },
    appMenu: {
      width: '100%',
      paddingTop: '16px',
    },
    socialMedia: {
    },
  })
});

const AppMenu = () => {
  const classes = useStyles()

  const data = useStaticQuery(graphql`
    query coreRuleQuery {
      allMdx(
        sort: { order: ASC, fields: [frontmatter___index] }
        limit: 1000
      ) {
        edges {
          node {
            frontmatter {
              slug
              title
              }
            }
          }
        }
      }
  `)

  const coreRulesTitles = data.allMdx.edges.map(item => {
    const nodeValues = item.node.frontmatter;
    return {'name': nodeValues.title, 'link': nodeValues.slug, 'linkType': 'internal'}
  })

  appMenuItems[5].items = coreRulesTitles

  return (
    <Grid container direction='column' className={classes.container}>
      <Grid item>
        <StaticImage 
          src='../img/open_legend_lg_logo.png'
          alt='Open Legend Logo'
          tag='section' 
          style={{ margin: '8px' }}
        />
      </Grid>
      <Grid item>
        <List component="nav" className={classes.appMenu} disablePadding>
          {appMenuItems.map((item, index) => (
            <AppMenuItem {...item} key={index} />
          ))}
        </List>
      </Grid>
      {/* <Grid item className={classes.socialMedia} > */}
        <SocialMedia  />
      {/* </Grid> */}
    </Grid>
  )
}

export default AppMenu;